<template>
<v-app>
  <v-container>
      <v-btn v-if="showDialog" @click="goToPath('DocumentsDialog')">
        Upload a new document 
      </v-btn>
      <v-text-field v-model="searchString" label="Search"></v-text-field>
      
        <v-radio-group v-model="sortBy" :mandatory="false" row label="Sorted by:">
          <v-radio :label="sortByOption" :value="sortByOption" v-for="sortByOption in sortByOptions" :key="sortByOption"></v-radio>
        </v-radio-group>
      <v-list>
        <v-list-tile v-for="(doc, index) in documents" :key=index>
            <v-list-tile-content @click="downloadDocument(doc.FILENAME)" @mouseover="mouseOver(doc.FILENAME)" class="pointer">
              <div class="flex-container">
                <div class="flex-item-name">{{doc.DESCRIPTION}}</div>
                <div class="flex-item-meta">{{doc.DATETIME}} by {{doc.AUTHOR}}</div>
              </div>
            </v-list-tile-content>
         </v-list-tile>
      </v-list>
  </v-container>
</v-app>
</template>

<script>
import mixins from '@/mixins'

export default {
  mixins: [mixins],
  data () {
    return {
      message: "This is documents page",
      showDialog: false,
      sortBy: "Datetime",
      searchString: "",
      sortByOptions: ['Author','Filename','Description','Datetime'],
    }
  },
  computed: {
    documents: function () {
      return this.$store.getters.documents
        .sort( this.compareDocuments )
        .filter ( this.filterDocuments )
    },
    sortDirection: function () {
      if ( this.sortBy === 'Datetime') {
        return "DESC"
      } else {
        return "ASC"
      }
    }  
  },
  methods: {
    goToPath: function(path) {
      this.$router.push(path)
    },
    mouseOver: function (fileName) {
      console.log(fileName)
    },
    downloadDocument (file){
      let filePath = "https://charisfellowship.us/fc/documents/"
      window.open(filePath+file)
    },
    filterDocuments (el) {
      if ( !this.searchString ) {return true}
      let searchAble = el.FILENAME + el.DESCRIPTION + el.AUTHOR + el.DATETIME
      if ( searchAble.toUpperCase().includes(this.searchString.toUpperCase())) {
        return true
      } else {
        return false
      }
    },
    compareDocuments (a,b) {
      let sortBy = this.sortBy.toUpperCase()
      console.log(sortBy)
      const aSortBy = a[sortBy].toUpperCase()
      const bSortBy = b[sortBy].toUpperCase()
      let comparison = 0
      if (aSortBy > bSortBy) { comparison = 1 }
      else if (aSortBy < bSortBy) { comparison = -1 }
      if ( this.sortDirection === "DESC" ) { comparison = comparison * -1 }
      return comparison
    },

  },
  created () {
    this.$store.dispatch('getDocuments')
  }
}

</script>

<style lang="stylus" scoped>
.flex-container {
  display:flex;
  width: 100% 
}

.flex-item-name {
  width:50%
}

.flex-item-meta {
  font-size: .8em;
  align-content: right;
  color: grey
}
</style>

